
import Crop from '../components/Crop'
import PhotoClip from 'photoclip'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { uploadImage } from '@/api/'
var EXIF = require('../utils/exif.js')
export default {
directives: {
TransferDom
},
props: {
name: {
type: String,
default: ''
},
isCrop: {
type: Boolean,
default: false
},
cropRadio: {
type: Number,
default: 1
},
limitSize: {
type: Number,
default: 0
}
},
data () {
return {
cropVisible: false,
canvas: null,
tCanvas: null,
ctx: null,
crop: null,
curFile: null
}
},
mounted () {
this.canvas = document.createElement('canvas')

this.tCanvas = document.createElement('canvas')
var fsize = parseInt(document.documentElement.style.fontSize)
this.clipWidth = Math.floor(6 * fsize)
this.clipHeight = Math.floor(6 * fsize * this.cropRadio)
if (!this.isCrop) {
return
}
this.crop = new PhotoClip(this.$refs.crop, {
size: [this.clipWidth, this.clipHeight],
lrzOption: {
quality: 0.8
},
style: {
maskBorder: '1px dashed #fff'
},
outputQuality: 1
})
},
methods: {
cancel () {
this.cropVisible = false
this.crop.clear()
},
finishClip () {
this.$vux.loading.show({
text: '正在上传'
})
var base64 = this.crop.clip()
uploadImage(base64, this.curFile.type, (res) => {
this.$vux.loading.hide()
this.$vux.toast.show({
text: res.msg
})
if (!res.error) {
this.$emit('on-clip', res.res)
}
})
this.cancel()
this.$vux.loading.hide()
},
fileChange (event) {
var file = event.target.files[0]
if (!file) {
return
}
this.curFile = file
let msg = this.isCrop ? '正在加载图片' : '正在上传'
this.$vux.loading.show({
text: msg
})
if (this.isCrop) {
this.crop.load(file)
this.cropVisible = true
this.$vux.loading.hide()
} else {
var _this = this
var reader = new FileReader()
reader.readAsDataURL(file) 
reader.onload = function () {
var base64 = this.result
var img = new Image()
img.onload = function () {

EXIF.EXIF.getData(img, function () {
var orientation = EXIF.EXIF.getTag(this, 'Orientation')
var _base64 = _this.compress(img, orientation)
uploadImage(_base64, _this.curFile.type, (res) => {
_this.$vux.loading.hide()
_this.$vux.toast.show({
text: res.msg
})
if (!res.error) {
_this.$emit('on-preview', res.res)
}
})
})
img = null
}
img.src = base64
}
}
},
compress (img, Orientation) {
var initSize = img.src.length
var width = img.width
var height = img.height
var canvas = this.canvas
var ctx = canvas.getContext('2d')
if (width > this.limitSize || height > this.limitSize) {
if (width >= height) {
height = ~~(this.limitSize * height / width)
width = this.limitSize
} else {
width = ~~(this.limitSize * width / height)
height = this.limitSize
}
}
canvas.width = width
canvas.height = height
ctx.fillStyle = '#fff'
ctx.fillRect(0, 0, canvas.width, canvas.height)
/* var tCanvas = this.tCanvas

var ratio
if ((ratio = width * height / 2000000) > 1) {
ratio = Math.sqrt(ratio)
width /= ratio
height /= ratio
} else {
ratio = 1
}
canvas.width = width
canvas.height = height
ctx.fillStyle = '#fff'
ctx.fillRect(0, 0, canvas.width, canvas.height)
var count
if ((count = width * height / 1000000) > 1) {
count = ~~(Math.sqrt(count) + 1)
var nw = ~~(width / count)
var nh = ~~(height / count)
tCanvas.width = nw
tCanvas.height = nh
for (var i = 0; i < count; i++) {
for (var j = 0; j < count; j++) {
ctx.drawImage(img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh)
ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh)
}
}
} else {
ctx.drawImage(img, 0, 0, width, height)
} */
ctx.drawImage(img, 0, 0, width, height)

if (Orientation !== '' && Orientation !== 1) {
switch (Orientation) {
case 6:// 需要顺时针（向左）90度旋转
this.rotateImg(img, 'left', canvas)
break
case 8:// 需要逆时针（向右）90度旋转
this.rotateImg(img, 'right', canvas)
break
case 3:// 需要180度旋转
this.rotateImg(img, 'right', canvas)
this.rotateImg(img, 'right', canvas)
break
}
}


var ndata = canvas.toDataURL('image/jpeg', 0.6)
console.log('压缩前：' + initSize)
console.log('压缩后：' + ndata.length)
console.log('压缩率：' + ~~(100 * (initSize - ndata.length) / initSize) + '%')

return ndata
},
rotateImg (img, direction, canvas) {

const minStep = 0
const maxStep = 3
if (img == null) {
return
}

let height = img.height
let width = img.width
let step = 2
if (step == null) {
step = minStep
}
if (direction === 'right') {
step++

step > maxStep && (step = minStep)
} else {
step--
step < minStep && (step = maxStep)
}

let degree = step * 90 * Math.PI / 180
let ctx = canvas.getContext('2d')
switch (step) {
case 0:
canvas.width = width
canvas.height = height
ctx.drawImage(img, 0, 0)
break
case 1:
canvas.width = height
canvas.height = width
ctx.rotate(degree)
ctx.drawImage(img, 0, -height)
break
case 2:
canvas.width = width
canvas.height = height
ctx.rotate(degree)
ctx.drawImage(img, -width, -height)
break
case 3:
canvas.width = height
canvas.height = width
ctx.rotate(degree)
ctx.drawImage(img, -width, 0)
break
}
}
},
components: {
Crop
}
}



