export default {
  model: {
    prop: 'visible',
    event: 'closeDialog'
  },
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    position: {
      type: String,
      default: 'middle'
    },
    title: {
      type: String,
      default: ''
    },
    cancelText: {
      type: String,
      default: '取消'
    },
    confirmText: {
      type: String,
      default: '确定'
    },
    bgTitle: {
      type: Boolean,
      default: false
    },
    bgSrc: {
      default: ''
    }
  },
  data () {
    return {
    }
  },
  mounted () {

  },
  computed: {
  },
  methods: {
    cancle () {
      this.$emit('closeDialog', false)
    },
    confirm () {
      this.$emit('onConfirm')
    }
  }
}



